import React from 'react'
import {connect} from 'react-redux'
import {Close, Tag1} from './tagDivStyle';
import * as actionCreators from "../actionCreators/contentActionCreators";

const TagDivWrap = (props) => {
  const {tags, closeTagDiv} = props;
  return (<div>
    <Close onClick={closeTagDiv}/>
    {
      tags.toJS().map((ele=>{
        return <Tag1 key={ele}>{ele}</Tag1>
      }))
    }
  </div>)

};
const mapStateToProps = (state) => {
  return {
    tags: state.contentReducer.get("tags"),
  }
};
const mapDispatchToProps = (dispatch) => {
  return {
    closeTagDiv: () => {
      const action = actionCreators.createCloseTagDivAction();
      dispatch(action);
    }
  }
};
export default connect(mapStateToProps, mapDispatchToProps)(TagDivWrap);
