import * as actionTypes from '../actionTypes/contentTypes'
import {fromJS} from 'immutable'

const defaultState = fromJS({
  value: '',
  arrowDirection: true,
  showDivPlace: '',
  showId: '',
  articles: [],
  tags: [],
  showTagContent: [
    {id: 1, content: [1111]},
    {id: 2, content: [2222]},
    {id: 3, content: [3333]},
    {id: 4, content: [4444]},
    {id: 5, content: [5555]},
    {id: 6, content: [6666]},
    {id: 7, content: [7777]},
    {id: 71, content: [77877]},
    {id: 72, content: [77777]},
    {id: 73, content: [67777]},
    {id: 74, content: [77775]},
    {id: 75, content: [77747]},
    {id: 76, content: [77377]},
    {id: 77, content: [72777]},
    {id: 781, content: [1777]},
    {id: 783, content: [17777]},
    {id: 7812, content: [117777]},
    {id: 7834, content: [117777]},
  ],
  menus:["Dashboard","All articles","Collection"]
});
export default function contentReducer(state = defaultState, action) {
  switch (action.type) {
    case actionTypes.ARROW_DIRECTION:
      return state.set('arrowDirection', !state.get('arrowDirection'));
    // case actionTypes.SHOW_TAG:
    //   return state.set('showDivPlace', constValue.SHOW_DIV_UP)
    //     .set('showId', action.id);
    // case actionTypes.CREATE_DOWN_DIV:
    //   return state.set('showDivPlace', constValue.SHOW_DIV_DOWN)
    //     .set('showId', action.id);
    case actionTypes.CLOSE_TAG_DIV:
      return state.set('showDivPlace', fromJS('')).set('showId',fromJS(''));
    case actionTypes.LOAD_MENU:
      return state.set('menus',fromJS(action.menus));
    case actionTypes.LOAD_ARTICLES:
      return state.set('articles',fromJS(action.articles));
    case actionTypes.LOAD_TAGS:
      return state.mergeDeep(action);
      // return state.set('tags',fromJS(action.tags)).set('showDivPlace',fromJS(action.upOrDown)).set('showId',fromJS(action.showId));
    default:
      return state
  }
}