import React, {Component} from 'react'
import {connect} from 'react-redux'
import * as actionCreators from '../actionCreators/contentActionCreators'
import * as constValue from '../constValue'
import {
  Content,
  ContentSide,
  ContentBody,
  Menu,
  Arrow,
  Body,
  TagDivUp,
  TagDivDown,
  H5
} from './contentStyle'
import TagDivWrap from './TagDivWrap'

class ContentWrap extends Component {
  componentDidMount() {
    const {loadMenus} = this.props;
    loadMenus();
  }

  render() {
    const {menus, showId, showDivPlace, articles, arrowClick, showArticleMsg} = this.props;

    return (<Content>
      <ContentSide>
        <Menu>
          {
            menus.toJS().map((ele) => {
              return (<H5 key={ele}><Arrow onClick={arrowClick}/>{ele}</H5>)
            })
          }
        </Menu>
      </ContentSide>
      <ContentBody>
        {
          articles.toJS().map((ele) => {
              return (<Body
                id={ele.id}
                // onClick={showArticleMsg}
                onDoubleClick={showArticleMsg}
                key={ele.id}>
              {ele.content}
              </Body>)
            }
          )
        }
        {showTagDiv(showDivPlace, showId)}
      </ContentBody>
    </Content>)
  }
}

function showTagDiv(showDivPlace) {
  console.log('showDivPlace', showDivPlace);
  if (showDivPlace === constValue.SHOW_DIV_UP) {
    return (<TagDivUp><TagDivWrap/></TagDivUp>);
  } else if (showDivPlace === constValue.SHOW_DIV_DOWN) {
    return (<TagDivDown><TagDivWrap/></TagDivDown>);
  } else {
    return null;
  }
}

const mapStateToProps = (state) => {
  return {
    articles: state.contentReducer.get("articles"),
    showDivPlace: state.contentReducer.get("showDivPlace"),
    showId: state.contentReducer.get("showId"),
    menus: state.contentReducer.get("menus")
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    arrowClick: () => {
      const action = actionCreators.arrowDirectionChangeAction();
      dispatch(action);
    },
    showArticleMsg: (e) => {
      const element = e.target;
      const id = element.id;
      const y = e.clientY;
      const innerHeight = window.innerHeight;
      let action,upOrDown;
      if (y / innerHeight < 0.5) {
        upOrDown = constValue.SHOW_DIV_DOWN;
      } else {
        upOrDown = constValue.SHOW_DIV_UP;
      }
      action = actionCreators.createShowTagAction(id,upOrDown);
      dispatch(action);
    },
    loadMenus: () => {
      const action = actionCreators.createFetchMenuAction();
      dispatch(action);
      const action_1 = actionCreators.createFetchArticlesAction();
      dispatch(action_1);
    }
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(ContentWrap);