import * as actionTypes from '../actionTypes/headTypes'
import {fromJS} from "immutable";

const defaultState = fromJS({value:'',sideMenu:true
});
export default function inputReducer(state = defaultState, action) {
  switch (action.type) {
    case actionTypes.INPUT_CHANGE:
      return state.set('value',action.value);
    case actionTypes.SIDE_MENU_STATUS:
      return state.set('sideMenu',!state.get('sideMenu'));
    default:
      return state
  }
}