import * as actionTypes from '../actionTypes/contentTypes'

export function arrowDirectionChangeAction() {
  return {
    type: actionTypes.ARROW_DIRECTION,
  }
}


export function createShowTagAction(id, upOrDown) {
  return {
    type: actionTypes.SHOW_TAG,
    id: id,
    upOrDown
  }
}

export function createFetchMenuAction() {
  return {
    type: actionTypes.FETCH_MENU
  }
}
export function createLoadMenuAction(menus) {
  return {
    type: actionTypes.LOAD_MENU,
    menus
  }
}

export function createCloseTagDivAction() {
  return {
    type: actionTypes.CLOSE_TAG_DIV
  }
}
export function createFetchArticlesAction() {
  return {
    type: actionTypes.FETCH_ARTICLES
  }
}
export function createLoadArticlesAction(articles) {
  return {
    type: actionTypes.LOAD_ARTICLES,
    articles
  }
}
export function createLoadTagsAction(tags,upOrDown,id) {
  return {
    type: actionTypes.LOAD_TAGS,
    tags:tags,
    showDivPlace:upOrDown,
    showId:id
  }
}