import React,{Component} from 'react'
import {connect} from 'react-redux'
import {Header, HeaderInput, ImageContainer} from './headerStyle'
import * as actionCreator from '../actionCreators/headerActionCreators'

class HeaderWrap extends Component {

  render() {
    const {inputValue, typeValue,toggleSideMenu} = this.props;
    return <Header>
      <ImageContainer onClick={toggleSideMenu}/>
      <HeaderInput value={inputValue} onChange={typeValue} />
    </Header>
  }
}

const mapStateToProps = (state) => {
  return {
    inputValue: state.inputReducer.value
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    typeValue: (e) => {
      const action = actionCreator.inputChangeAction(e.target.value);
      dispatch(action);
    },
    toggleSideMenu: () => {
      const action = actionCreator.toggleSideMenuAction();
      dispatch(action);
    }
  }
};


export default connect(mapStateToProps, mapDispatchToProps)(HeaderWrap);